@import "./../base/vars";
.answers {
  &__title {
    margin-top: 70px;
    font-family: Lato Bold;
    font-size: 72px;
    color: $accentColor;
    text-align: center;
    @media (max-width: 1300px) {
      font-size: 46px;
    }
    @media (max-width: 992px) {
      font-size: 40px;
      margin-top: 40px;
    }
  }
  &__text--top {
    font-family: Lato-Italic;
    font-size: 20px;
    text-align: center;
    margin: 20px 0 60px;
  }
  &__subtitle {
    font-family: Lato Bold;
    font-size: 23px;
    margin-top: 36px;
  }
  &__text {
    font-family: Lato-Regular;
    font-size: 16px;
    color: #8d8f94;
    margin-top: 36px;
  }
  &__btn {
    width: 238px;
    height: 72px;
    background-color: #02a0e3;
    color: #ffffff;
    border-radius: 5px;
    -webkit-border-radius: 5px;
    -moz-border-radius: 5px;
    -ms-border-radius: 5px;
    -o-border-radius: 5px;
    font-family: Lato-Medium;
    font-size: 24px;
    box-shadow: 0px 4px 18px rgba(52, 119, 196, 0.35),
      inset 0px 1px 0px rgba(255, 255, 255, 0.3);
    margin-top: 48px;
  }
  &__btn:hover {
    background-color: #5eb0f3;
  }
  &__button {
    display: flex;
    justify-content: center;
  }
  &__summary__img {
    position: relative;
    top: 20px;
    width: 15px;
    height: 15px;
  }
}
details > summary {
  list-style-type: none;
  background-color: #f6f7fb;
}

details > summary::-webkit-details-marker {
  display: none;
}
summary {
  display: flex;
  flex-direction: row-reverse;
  justify-content: space-between;
  align-items: center;
}
details[open] {
  background-color: #f6f7fb;
  img {
    transform: rotate(45deg);
    -webkit-transform: rotate(45deg);
    -moz-transform: rotate(45deg);
    -ms-transform: rotate(45deg);
    -o-transform: rotate(45deg);
  }
}
