@import "./../base/vars";

.sticky {
  width: 100%;
  position: fixed;
  top: 20px;
  background-color: #0191b6;
  z-index: 100;
  display: none;
  @media all and (max-width: 994px) {
    display: none;
  }

  &__nav {
    display: flex;
    align-items: center;
    justify-content: space-between;
    max-width: 1170px;
    padding-top: 10px;
    margin: 0 auto;
  }
  &__nav__item {
    font-family: Lato SemiBold;
    font-size: 20px;
    color: white;
    margin-left: 20px;
  }
  &__phone {
    font-size: 36px;
    font-family: Lato-Black;
    color: #91d2df;
    margin-left: 56px;
  }
  &__phone__link {
    font-size: 18px;
    font-family: Lato-Ligh;
    color: #91d2df;
    border-bottom: solid 1px #91d2df;
  }
  &__header__phone {
    display: flex;
    align-items: center;
    margin-top: 24px;
  }
  &__nav__list {
    display: flex;
  }
  &__wrap {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
  }
  &__container {
    padding: 0 100px 0;
    max-width: 1400px;
    margin: 0 auto;
  }
}
