@import "./../base/vars";
.rates {
  margin-top: 100px;
  &__title {
    font-family: Lato Bold;
    font-size: 72px;
    color: $accentColor;
    text-align: center;
    @media (max-width: 1300px) {
      font-size: 46px;
    }
    @media (max-width: 992px) {
      font-size: 40px;
    }
  }
  &__subtitle {
    font-family: Lato-Italic;
    font-size: 20px;
    text-align: center;
    letter-spacing: 0.08rem;
    margin-top: 30px;
    @media (max-width: 992px) {
      margin-top: 20px;
    }
  }
}
